import { __ } from '@wordpress/i18n';
const IS_PRO = !!window.arkbSettings?.pfkey || false;

const onlyProMessage = (
	<p className='arkb-proOnlyArea__message'>
		{__('This setting is only available in the PRO version.', 'arkhe-blocks')}
		<a
			className='arkb-proOnlyArea__link'
			href='https://arkhe-theme.com/ja/product/arkhe-blocks-pro/'
			target='_blank'
			rel='noreferrer noopener'
		>
			Go Pro
		</a>
	</p>
);

const onlyProEditMessage = (
	<p className='arkb-proOnlyArea__message'>
		{__('This area can only be edited in the PRO version.', 'arkhe-blocks')}
		<a
			className='arkb-proOnlyArea__link'
			href='https://arkhe-theme.com/ja/product/arkhe-blocks-pro/'
			target='_blank'
			rel='noreferrer noopener'
		>
			Go Pro
		</a>
	</p>
);
/**
 * ArkbProOnly
 */
export default ({ type = '', passCheck = false, children }) => {
	if (passCheck) return children;

	return (
		<>
			{!IS_PRO ? (
				<div className='arkb-proOnlyArea'>
					{'edit' === type ? onlyProEditMessage : onlyProMessage}
					<div className='arkb-proOnlyArea__preview'>{children}</div>
				</div>
			) : (
				children
			)}
		</>
	);
};
