/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useEffect, useState } from '@wordpress/element';
import { registerBlockType } from '@wordpress/blocks';
import { BlockControls, InspectorControls, useBlockProps } from '@wordpress/block-editor';
import { PanelBody, ToggleControl, TextControl, TextareaControl } from '@wordpress/components';
import ServerSideRender from '@wordpress/server-side-render';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import blockIcon from './icon';
import example from './_example';
import exampleHtml from './_exampleHtml';
import { ArkbProOnly, ArkbMarginControl, ArkbLinkControl } from '@components';

const IS_PRO = !!window.arkbSettings?.pfkey || false;

/**
 * registerBlockType
 */
const blockName = 'ark-block-blogCard';
registerBlockType(metadata.name, {
	title: __('Blog card', 'arkhe-blocks'),
	description: __('Create a card-type link for related articles.', 'arkhe-blocks'),
	icon: blockIcon,
	example,
	edit: ({ attributes, setAttributes, isSelected }) => {
		const {
			isPreview,
			useCache,
			linkData,
			isNewTab,
			rel,
			caption,
			customTitle,
			customExcerpt,
			showExerptPC,
			showExerptSP,
			showImage,
		} = attributes;

		// プレビュー中
		if (isPreview) return exampleHtml;

		// 旧データの変換
		useEffect(() => {
			if (attributes.postId) {
				setAttributes({
					postId: undefined,
					linkData: { id: attributes.postId, kind: 'post-type' },
				});
			} else if (attributes.externalUrl) {
				setAttributes({
					externalUrl: undefined,
					linkData: { url: attributes.externalUrl },
				});
			}
		}, []);

		// 現在の記事のID
		// const currentID = useSelect((select) => select('core/editor').getCurrentPostId());
		// const blockEdit = ();

		const setLink = (changedVal) => {
			const { id, url, kind, type, opensInNewTab } = changedVal;

			if (id) {
				// kind : "taxonomy", "post-type", type: 投稿タイプ or タクソノミー名
				setAttributes({ linkData: { id, kind, type, url } });
			} else if (url) {
				setAttributes({ linkData: { url } });
			}

			if (typeof opensInNewTab !== 'undefined') {
				setAttributes({
					isNewTab: opensInNewTab,
					rel: changedVal.rel,
				});
			}
		};

		const removeLink = () => {
			setAttributes({
				linkData: undefined,
				isNewTab: false,
				rel: undefined,
			});
		};

		const [isLinkOpen, setIsLinkOpen] = useState(false);
		const hasLink = linkData?.id || linkData?.url;

		if (!hasLink && isSelected && !isLinkOpen) {
			setIsLinkOpen(true);
		}

		return (
			<>
				<BlockControls group='block'>
					<ArkbLinkControl
						attrs={{ url: linkData?.url, rel, isNewTab }}
						setLink={setLink}
						removeLink={removeLink}
						setRel={(val) => {
							setAttributes({ rel: val || undefined });
						}}
						isLinkOpen={isLinkOpen}
						setIsLinkOpen={setIsLinkOpen}
					/>
				</BlockControls>
				<BlockControls>
					<ArkbMarginControl {...{ className: attributes.className, setAttributes }} />
				</BlockControls>
				<InspectorControls>
					<PanelBody title={__('Settings', 'arkhe-blocks')} initialOpen={true}>
						<ToggleControl
							label={__('Use the cache', 'arkhe-blocks')}
							help={__(
								'If you want to clear the cache, turn it off only once.',
								'arkhe-blocks'
							)}
							checked={useCache}
							onChange={(val) => {
								setAttributes({ useCache: val });
							}}
							className='arkb-ctrl--mb--s'
						/>
						<ArkbProOnly>
							<ToggleControl
								label={__('Show featured image', 'arkhe-blocks')}
								checked={showImage}
								onChange={(val) => {
									if (!IS_PRO) return;
									setAttributes({ showImage: val });
								}}
								className='arkb-ctrl--mb--s'
							/>
							<ToggleControl
								label={__('Show excerpt', 'arkhe-blocks') + ' (PC)'}
								checked={showExerptPC}
								onChange={(val) => {
									if (!IS_PRO) return;
									setAttributes({ showExerptPC: val });
								}}
								className='arkb-ctrl--mb--s'
							/>
							<ToggleControl
								label={__('Show excerpt', 'arkhe-blocks') + ' (SP)'}
								checked={showExerptSP}
								onChange={(val) => {
									if (!IS_PRO) return;
									setAttributes({ showExerptSP: val });
								}}
							/>
							<TextControl
								label={__(
									'Caption at the bottom right of the card',
									'arkhe-blocks'
								)}
								value={caption || ''}
								onChange={(val) => {
									if (!IS_PRO) return;
									setAttributes({ caption: val || undefined });
								}}
							/>
							<TextControl
								label={__('Overwrite title', 'arkhe-blocks')}
								value={customTitle || ''}
								onChange={(val) => {
									if (!IS_PRO) return;
									setAttributes({ customTitle: val || undefined });
								}}
							/>
							<TextareaControl
								label={__('Overwrite excerpt', 'arkhe-blocks')}
								value={customExcerpt || ''}
								onChange={(val) => {
									if (!IS_PRO) return;
									setAttributes({ customExcerpt: val || undefined });
								}}
							/>
						</ArkbProOnly>
					</PanelBody>
				</InspectorControls>
				<div {...useBlockProps()}>
					{hasLink ? (
						<ServerSideRender
							block={metadata.name}
							attributes={attributes}
							className={`${blockName}__preview`}
						/>
					) : (
						<div className={`${blockName}__preview -none ark-block-blogCard`}>
							{`※ ${__('Please specify the link.', 'arkhe-blocks')}`}
						</div>
					)}
				</div>
			</>
		);
	},
	save: () => {
		return null;
	},
});
