/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import {
	ToolbarButton,
	ToolbarGroup,
	Popover,
	TextControl,
	ToggleControl,
} from '@wordpress/components';
import { useState } from '@wordpress/element';
import { Icon, link } from '@wordpress/icons';
import { LinkControl } from '@compatible';

/**
 * @Internal dependencies
 */

import { arkbGlbStr, getNewLinkRel } from '@helper';
import { ArkbProOnly } from '@components';

const ArkbLinkControl = ({
	attrs,
	setLink,
	removeLink,
	setRel,
	isLinkOpen, // state
	setIsLinkOpen = null, // set state
	setAriaLabel = null,
	setUseAtag = null,
	isPro = false,
}) => {
	const { url, rel, isNewTab, ariaLabel, useAtag } = attrs;

	let isOpen;
	let setIsOpen;

	if (null === setIsLinkOpen) {
		[isOpen, setIsOpen] = useState(false);
	} else {
		isOpen = isLinkOpen;
		setIsOpen = setIsLinkOpen;
	}

	const hasURL = !!url;

	const controls = (
		<>
			<LinkControl
				value={{ url, opensInNewTab: isNewTab }}
				onChange={(changedVal) => {
					setLink({
						...changedVal,
						rel: getNewLinkRel(changedVal?.opensInNewTab, rel),
					});
				}}
				onRemove={() => {
					removeLink();
					setIsOpen(false);
				}}
				forceIsEditingLink={!hasURL} // URLを編集モードにするかどうか
				renderControlBottom={() => {
					return (
						<div className='arkb-link-control__bottom'>
							<TextControl
								label={__('Link rel')}
								value={rel || ''}
								onChange={(value) => {
									setRel(value);
								}}
							/>
							{setAriaLabel && (
								<TextControl
									label='aria-label'
									value={ariaLabel || ''}
									onChange={(newLabel) => {
										setAriaLabel(newLabel);
									}}
								/>
							)}
							{setUseAtag && (
								<ToggleControl
									// className='u-arkb--mt--15'
									label={__('Make the parent element an <a> tag', 'arkhe-blocks')}
									help={
										arkbGlbStr('NOTE') +
										__(
											'Placing more <a> tags inside the block breaks the display.',
											'arkhe-blocks'
										)
									}
									checked={useAtag || false}
									onChange={(value) => {
										setUseAtag(value);
									}}
								/>
							)}
						</div>
					);
				}}
			/>
		</>
	);

	return (
		<>
			<ToolbarGroup>
				<ToolbarButton
					name='link'
					icon={<Icon icon={link} />}
					title={__('Link')}
					onClick={() => {
						setIsOpen(true);
					}}
					isActive={hasURL}
				/>
			</ToolbarGroup>
			{isOpen && (
				<Popover
					className='arkb-link-control'
					position='bottom center'
					onClose={() => setIsOpen(false)}
				>
					{isPro ? <ArkbProOnly>{controls}</ArkbProOnly> : controls}
				</Popover>
			)}
		</>
	);
};
export default ArkbLinkControl;
