/**
 * @WordPress dependencies
 */
import { __, sprintf } from '@wordpress/i18n';

/**
 * グローバル文字列を取得
 *
 * @param {string} type
 * @param {string} args
 * @return {string} 翻訳テキスト
 */
export default function arkbGlbStr(type, ...args) {
	const TEXT_LIST = {
		NOTE: __('Note: ', 'arkhe-blocks'),
		ADD: __('Add %s', 'arkhe-blocks'),
		CLOSE: __('Close %s', 'arkhe-blocks'),
		OPEN: __('Open %s', 'arkhe-blocks'),
		CONF_RESET: __('Do you want to reset %s?', 'arkhe-blocks'),
		HAS_ERROR: __('There is an error in %s', 'arkhe-blocks'),
		TO_SELECT: __('Select %s', 'arkhe-blocks'),
		TO_INPUT: __('Enter %s', 'arkhe-blocks'),
		NOT_FOUND: __('%s not found.', 'arkhe-blocks'),
		NOT_VALID: __('%s is incorrect.', 'arkhe-blocks'),
		NO_DATA: __('Currently, %s is not yet registered.', 'arkhe-blocks'),
	};

	/* eslint @wordpress/i18n-translator-comments: 0 */
	switch (true) {
		case type === 'ADD':
		case type === 'CLOSE':
		case type === 'OPEN':
		case type === 'CONF_RESET':
		case type === 'HAS_ERROR':
		case type === 'TO_SELECT':
		case type === 'TO_INPUT':
		case type === 'NOT_FOUND':
		case type === 'NOT_VALID':
		case type === 'NO_DATA':
			const text = TEXT_LIST[type] || '';
			return sprintf(text, ...args);
		default:
			return TEXT_LIST[type] || '';
	}
}
